@import "../node_modules/@syncfusion/ej2-base/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-buttons/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-inputs/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-popups/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-react-calendars/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-react-lists/styles/fabric.css";

body {
  margin: 0px;
  padding: 0px;
  min-height: 700px;
}

.e-left:before {
  content: "\e85b";
}


.e-listview .e-list-item {
  background-color: inherit;
  border-bottom: 1px solid #707070;
  border-left: 1px solid #707070;
  border-right: 1px solid #707070;
  border-top: 1px solid#707070;
  margin: 10px;
}

.e-listview .e-list-item.e-hover, .e-listview .e-list-item.e-hover.e-active.e-checklist {
  border-color: #707070;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  min-height: 700px;
}

.header {
  display: flex;
  flex-direction: row;
  height: 10vh;
  width: 100vw;
  min-height: 70px;
}

.header-text {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5vw;
  font-weight: bold;
  min-height: 70px;
}

.nav {
  height: 10vh;
  width: 10vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5vw;
  min-height: 70px;
}

.progress-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 10vh;
  width: 100vw;
  min-height: 100px;
}

.progress-textarea {
  font-size: 3vw;
}

.content-container {
  flex: 1;
  display: flex;
  flex-direction: row;
}

.left-margin,
.right-margin {
  width: 10vw;
}

.content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.content-textarea {
  font-size: 5vw;
}

.sub-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.date-time-pick {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 10px 20px 10px
}

.time-list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0px 20px 0px;
}

@media (min-width: 500px) {
  .content-textarea {
    font-size: 3vw;
  }
}

@media (min-width: 700px) {
  .content-textarea {
    font-size: 2vw;
  }
  .left-margin, .right-margin {
    width: 29vw;
  }
}

@media (max-width: 500px) and (max-height: 700px) {
  .nav {
    height: 10vh;
    width: 20vw;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4vw;
    min-height: 70px;
  }

  .header-text {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4vw;
  }

  .progress-textarea {
    font-size: 4vw;
  }

  .content-textarea {
    font-size: 5vw;
  }
}


